/**
 * WordPress Dependencies
 */
import { createReduxStore, register } from "@wordpress/data";
/**
 * Internal dependencies
 */
import { stores } from "@/globalConstant";
// Initial state for the store
const initialState = {
    dateRange: {
        from: new Date(),
        to: new Date(),
    },
    selectedBranch: "all",
    isLoading: false,
    overview: null,
    reservations: {
        data: [],
        isLoading: false,
        error: null,
    },
    orders: {
        data: [],
        isLoading: false,
        error: null,
    },
    topSales: {
        data: [],
        isLoading: false,
        error: null,
    },
};
// Action types
const actionTypes = {
    UPDATE_STATE: "UPDATE_STATE",
};
// Create the store
const store = createReduxStore(stores === null || stores === void 0 ? void 0 : stores.dashboard, {
    // Reducer to handle state updates
    reducer: (state = initialState, action) => {
        switch (action.type) {
            case actionTypes.UPDATE_STATE:
                return Object.assign(Object.assign({}, state), action.payload);
            default:
                return state;
        }
    },
    // Actions for dispatching updates
    actions: {
        setDashboardState(updates) {
            return { type: actionTypes.UPDATE_STATE, payload: updates };
        },
    },
    // Selectors for accessing state
    selectors: {
        getDashboardState(state) {
            return state;
        },
    },
});
// Register the store
register(store);
