/**
 * WordPress Dependencies
 */
import { allSteps } from "@/admin/features/onboard/constants";
import { stores } from "@/globalConstant";
import { createReduxStore, register } from "@wordpress/data";
// Initial state for the store
const initialState = {
    currentStep: 1,
    loading: false,
    stepsCompleted: false,
    steps: allSteps,
    totalSteps: Object.keys(allSteps).length,
    data: {},
};
// Action types
const actionTypes = {
    UPDATE_STATE: "UPDATE_STATE", // Unified action type for updating state
    RESET_STATE: "RESET_STATE", // Action type for resetting state
};
// Create the store
const store = createReduxStore(stores === null || stores === void 0 ? void 0 : stores.onboard, {
    // Reducer to handle state updates
    reducer: (state = initialState, action) => {
        switch (action.type) {
            case actionTypes.UPDATE_STATE:
                return Object.assign(Object.assign({}, state), action.payload); // Merge updates into the state
            case actionTypes.RESET_STATE:
                return Object.assign({}, initialState); // Reset state to initial values
            default:
                return state;
        }
    },
    // Actions for dispatching updates
    actions: {
        setOnboardState(updates) {
            return { type: actionTypes.UPDATE_STATE, payload: updates };
        },
        resetOnboard() {
            return { type: actionTypes.RESET_STATE }; // Action for resetting state
        },
    },
    // Selectors for accessing state
    selectors: {
        getOnboardState(state, key) {
            return key ? state[key] : state; // Return the full state or a specific key
        },
    },
});
// Register the store
register(store);
